import React from 'react';
import { connect } from 'react-redux';
import "./Home.css"
import { TimelineLite, Elastic, Expo, CSSPlugin } from 'gsap/all';
import { SplitText } from "../../gsap/src/bonus-files-for-npm-users/SplitText"      

const C = CSSPlugin;//A little gotcha when uploading to github pages and animation doesn't work


class Home extends React.Component {
    constructor() {
        super();

        this.ourNames = null;
        this.sinceDate = null;
        this.homeDivBody = null;
    }

    componentDidMount() {
        this.splitText = new SplitText(this.ourNames, {type: "chars"});
        this.headerTextTl = new TimelineLite({ paused: true })
            .staggerFromTo(this.splitText.chars, 2, {y:"-400"}, {y:"0", ease: Elastic.easeInOut}, 0.2)
            .from(this.sinceDate, 1, {x: "500"})
            .play()

        this.openCloseNav = new TimelineLite({ paused: true })
            .to(this.homeDivBody, 1.5, { right: "-210", ease: Expo.easeIn})
            .to({}, 0.3, {})
    }

    componentDidUpdate() {
        if (this.props.nav.navIsOpen === true) {
            this.openCloseNav.play()
        }else {
            this.openCloseNav.reverse()
        }
    }

    render() {
        return (
            <div className="hpBodyDiv" ref={ div => this.homeDivBody = div }>
                <div className="hpHeaderDiv">
                    <h1 ref={ h1 => this.ourNames = h1 }>Nick &amp; Shera</h1>
                    <h3 ref={ h3 => this.sinceDate = h3 }>since 03'</h3>
                </div>
                <div>
                    <div className="placeHolder"></div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        nav: state.nav
    };
};
  
const mapDispatchToProps = (dispatch) => {
    return {
        changeNavState: (navstate) => {
            dispatch({
                type: "NAV_STATE",
                payload: navstate
            });
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(Home)