import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { TweenMax, TimelineLite, Expo, Elastic, CSSPlugin } from 'gsap/all';
import { MorphSVGPlugin } from "../../gsap/src/bonus-files-for-npm-users/MorphSVGPlugin";
import './MobileNav.css';

const C = CSSPlugin;//A little gotcha when uploading to github pages and animation doesn't work

class MobileNav extends React.Component {
    constructor() {
        super();

        this.morphSVG = MorphSVGPlugin;
        this.openCloseSidebar = this.openCloseSidebar.bind(this);
        this.sidebar = null;
        this.sidebarBg = null; 
        this.naviLinks = null;
        this.fixedTitle = null;
        this.rightArrow1 = null;
        this.rightArrow2 = null;
        this.leftArrow1 = null;
        this.leftArrow2 = null;
        this.sidebarTween = null; 
    }

    componentDidMount () {
        this.sidebarTween = new TimelineLite({ paused: true})
            .to(this.sidebar, 0.1, {display: "block"})
            .to(this.sidebarBg, 1.4, {
                x: "-45%",
                ease: Expo.easeIn
            })
            // .to(this.sidebarBg, 2, {
            //     attr: {
            //         d:"M24-48H391.837C193.4,367.237,565.913,513.078,391.473,1032.037c0,.053-367.473,0-367.473,0Z",
            //         d:"M24-48H391.837c-.037-.036-.361,1080-.363,1080.037,0,.053-367.473,0-367.473,0Z"
            //     },
            //     ease: Expo.easeOut
            // }, "-=1")
            .to(this.fixedTitle, 1.5, {right: "-210", ease: Expo.easeIn}, "0")
            .to(this.rightArrow1, 0.5, {morphSVG: this.leftArrow1}, "-=0.2")
            .to(this.rightArrow2, 0.5, {morphSVG: this.leftArrow2}, "-=0.3")
            .to(this.naviLinks, 0.2, {display: "block"}, "+=0.1")
            .reverse()
    }

    openCloseSidebar() {
        this.sidebarTween.reversed() ? this.sidebarTween.play() : this.sidebarTween.reverse(0);
        console.log(this.props.nav);
        this.props.changeNavState(!this.props.nav.navIsOpen)

    }

    render() {
        return ( 
            <div>
                <div ref={div => this.fixedTitle = div} id="fixedNaviBar">
                    <div onClick={this.openCloseSidebar} id="svgDiv">
                        <svg id="rightarrow" xmlns="http://www.w3.org/2000/svg" width="26.004" height="25.84" viewBox="0 0 26.004 25.84">
                            <path ref={ path => this.rightArrow1 = path} id="Path_1" data-name="Path 1" d="M13.049.58,10.965,2.664,21.8,13.5,10.965,24.336l2.084,2.084L25.969,13.5Z" transform="translate(-10.965 -0.58)" fill="#0F4279"/>
                            <path ref={ path => this.rightArrow2 = path} id="Path_1" data-name="Path 2" d="M2.049.58-.035,2.664,10.8,13.5-.035,24.336,2.049,26.42,14.969,13.5Z" transform="translate(11.035 -0.58)" fill="#0F4279"/>
                            <path ref={path => this.leftArrow1 = path} id="Path_1a" data-name="Path 3" d="M23.885.58l2.084,2.084L15.133,13.5,25.969,24.336,23.885,26.42,10.965,13.5Z" fill="#0F4279"/>
                            <path ref={path => this.leftArrow2 = path} id="Path_1a" data-name="Path 4" d="M12.885.58l2.084,2.084L4.133,13.5,14.969,24.336,12.885,26.42-.035,13.5Z" fill="#0F4279"/>
                        </svg> 
                    </div>
                    <div id="webPageTitle"><a href="/">Nick and Shera</a></div>
                </div>
                
                <div ref={ div => this.sidebar = div } id="mobilenav">
                    <svg className="waveySidebar" preserveAspectRatio="xMidYMid slice">
                        <path ref={path => this.sidebarBg = path} id="start" d="M24-48H391.837c-.037-.036-.361,1080-.363,1080.037,0,.053-367.473,0-367.473,0Z" transform="translate(-420 0)" fill="#ffffff" />
                    </svg>
                    <nav ref={ nav => this.naviLinks = nav} id="navi">
                        <div className="linksDiv">
                            <ul id="linksList">
                                <Link to="/">
                                    <li>Home</li>
                                </Link>
                                <Link to="/about">
                                    <li>About</li>
                                </Link>
                                <Link to="/contact">
                                    <li>Contact</li>
                                </Link>
                                <Link to="/podcast">
                                    <li>Podcast</li>
                                </Link>
                                <Link to ="/event">
                                    <li>Event</li>
                                </Link>
                            </ul>
                        </div>  
                    </nav>
                </div>    
            </div>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        nav: state.nav
    };
  };
  
const mapDispatchToProps = (dispatch) => {
    return {
        changeNavState: (navstate) => {
            dispatch({
                type: "NAV_STATE",
                payload: navstate
            });
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(MobileNav) 